import { Component, OnInit } from '@angular/core';
import { ApicallService } from 'src/app/services/CallApi/apicall.service';
import { GlobalService } from 'src/app/services/data/global.service';
import { AlertController } from '@ionic/angular';
@Component({
  selector: 'app-add-accu-data',
  templateUrl: './add-accu-data.page.html',
  styleUrls: ['./add-accu-data.page.scss'],
})
export class AddAccuDataPage implements OnInit {


  public accountData: any = { type: '', stype: '', source: '', description: '', credit: '', debit: '', o_id:'' }
  handlerMessage: string;
  // Transaction Variables by Zagham Nadeem
  public transaction:any = {from:'', to:'', debit:'', credit:'', type:'', description:''}
  public owners:any
  public selectTabs:any = "Bank";
  type: any;
  constructor(public apicall: ApicallService, public alert: AlertController, public global: GlobalService) { }

  ngOnInit() {
    this.getOwner();
  }

  //  Get Owners
  async getOwner() {
    await this.apicall.api_getOwner();
    await this.global.Owner.subscribe( owner => {
      this.owners = owner;
    })
  }
  //  Select Owner
  selectFrom($event) {
    console.log($event.target.value);
    this.transaction.from = $event.target.value.id;
    console.log(this.transaction)

  }
  selectTo($event) {
    console.log($event.target.value);
    this.transaction.to = $event.target.value.id;
    console.log(this.transaction)

  }
  // Select Account Transaction Type
  selectTypeTransaction($event) {
    console.log($event.target.value);
    this.type = $event.target.value;
    this.transaction.type = $event.target.value;
    this.transaction.to = this.transaction.from;
  }
  async submitAccounts() {
    const alert = await this.alert.create({
      message: 'Are you sure?',

      buttons: [
        {
          text: 'Cancel',
          role: 'cancel',
          handler: () => {
            this.handlerMessage = 'Alert canceled';
          },
        },
        {
          text: 'OK',
          role: 'confirm',
          handler: () => {
            this.handlerMessage = 'Alert confirmed';
            if (this.accountData.credit == '') {
              this.accountData.credit = 0;
              console.log(this.accountData.credit);
            }
            else if (this.accountData.debit == '') {
              this.accountData.debit = 0;
              console.log(this.accountData.debit);
            }
            console.log(this.transaction);
            this.apicall.api_insert_accounts(this.transaction)
            this.transaction = { from: '',to:'' , type: '', description: '', credit: '', debit: ''}

          },
        },
      ],

    });

    await alert.present();
  }

  submitKhata(){
    
  }

  // type

  // salery, expense, school expence, income, 

  // source

  // honor, bank, employ

}
